.join {
  width: 100%;
  padding-top: 80px;
  display: flex;
  flex-direction: column;
  padding-left: 402px;
}

.join-main {
  width: 100%;
  display: flex;
  flex-direction: column;

}

.join-main::after {
  content: '';
  width: 454px;
  height: 402px;
  background: url(@/assets/join-test-bg.svg) no-repeat;
  background-size: contain;
  left: 50%;
  top: 165px;
  transform: translate3d(-50%, 0, 0);
  position: absolute;
}

.join-logo {
  width: 108px;
  height: 22px;
  margin-top: 176px;
  margin-bottom: 32px;
  z-index: 1;
}

.join-title {
  color: #fff;
  font-weight: 500;
  font-size: 36px;
  line-height: 56px;
  height: 56px;
  margin-bottom: 32px;
  z-index: 1;
}

.checkbox-group {
  margin-bottom: 20px;
}

.checkbox-group label {
  z-index: 1;
}

.button-group {
  display: flex;
  align-items: center;
}

.button-group > button:first-child {
  border: none;
  margin-right: 12px;
}

.button-group > button:nth-child(2) {
  background: none;
  border: 1px solid #fff;
  opacity: 0.5;
}


@media screen and (max-width: 1440px) {
  .join {
    padding-top: 56.8px;
    padding-left: 285px;
  }



.join-main::after {
  content: '';
  width: 322px;
  height: 285px;
  background: url(@/assets/join-test-bg.svg) no-repeat;
  background-size: contain;
  left: 50%;
  top: 117px;
  transform: translate3d(-50%, 0, 0);
  position: absolute;
}

.join-logo {
  width: 76.8px;
  height: 15.6px;
  margin-top: 125px;
  margin-bottom: 22.75px;
  z-index: 1;
}

.join-title {
  color: #fff;
  font-weight: 500;
  font-size: 36px;
  line-height: 56px;
  height: 56px;
  margin-bottom: 32px;
  z-index: 1;
}


  .join-main::after {
    max-width: 454px;
    width: 100%;
  }


  .join-title {
    font-size: 26px;
    line-height: 26px;
    height: 32px;
    margin-bottom: 24px;
  }
}


@media screen and (max-width: 768px) and (min-width: 320px) {
  .join {
    padding-top: 1.64rem;
    padding-left: 0;
  }

  .join-main {
    align-items: center;
  }

  .join-main::after {
    width: 3.75rem;
    height: 3.75rem;
    max-height: 402px;
    max-width: 454px;
    top: 1.65rem;
  }

  .join-logo {
    margin-top: 0rem;
    margin-bottom: .2rem;
  }


  .join-title {
    font-size: .26rem;
    line-height: .36rem;
    width: 2.995rem;
    height: .72rem;
    text-align: center;
    margin-bottom: .24rem;
  }

  .button-group {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding: 0 .33rem;
  }

  .button-group > button:first-child {
      margin-right: 0;
      margin-bottom: .12rem;
      width: 100%;
      height: .42rem;
  }

  .button-group > button:nth-child(2) {
      height: .42rem;
      width: 100%;
  }
}
